import React, { Component } from 'react'
import '../Typography/Typography.css'

import PeriodSelector from '../PeriodSelector/PeriodSelector.js'
import PeriodCalendar from '../PeriodCalendar/PeriodCalendar.js'
import Calendar from '../Calendar/Calendar.js'

import axios from 'axios'
import Cookies from 'js-cookie'

import { BarChart, Bar, XAxis, LabelList } from 'recharts'

import moment from 'moment'

const fakeInterval = { start: "2018-05-01", end: "2018-05-31", intervalType: 'days' };
const rnd = (start, end) => Math.floor(Math.random() * end) + start

function cleanData(stats) {
    return stats.map(row => {
        return {
            date: row.date,
            'Входящие': parseInt(row['Входящие']),
            'Пропущенные': parseInt(row['Пропущенные'])
        }
    })
}

function getCurrentWeek() {
    return {
        start: moment().startOf('week').format('YYYY-MM-DD'),
        end: moment().endOf('week').format('YYYY-MM-DD'),
        intervalType: 'days'
    }
}

const fakeData = () => [
    { date: '01 дек.', 'Входящие': rnd(10, 24), 'Пропущенные': 9 },
    { date: '02 дек.', 'Входящие': rnd(10, 43), 'Пропущенные': 9 },
    { date: '03 дек.', 'Входящие': rnd(10, 26), 'Пропущенные': 8 },
    { date: '04 дек.', 'Входящие': rnd(10, 46), 'Пропущенные': 5 },
    { date: '05 дек.', 'Входящие': rnd(10, 66), 'Пропущенные': 11 },
    { date: '06 дек.', 'Входящие': rnd(10, 59), 'Пропущенные': 11 },
    { date: '07 дек.', 'Входящие': rnd(10, 45), 'Пропущенные': 8 },
    { date: '08 дек.', 'Входящие': rnd(10, 37), 'Пропущенные': 10 },
    { date: '09 дек.', 'Входящие': rnd(10, 53), 'Пропущенные': 19 },
    { date: '10 дек.', 'Входящие': rnd(10, 33), 'Пропущенные': 8 },
    { date: '11 дек.', 'Входящие': rnd(10, 58), 'Пропущенные': 11 },
    { date: '12 дек.', 'Входящие': rnd(10, 55), 'Пропущенные': 9 }
]

const settings = { top: 30, right: 0, left: 0, bottom: 0 };


class SimpleBarChart extends React.Component {
    render() {
        return (
            <BarChart width={800} height={350} data={this.props.data} margin={settings}>
                <XAxis dataKey="date" axisLine={false} tickLine={false} />
                <Bar dataKey="Входящие" fill="#00D05D">
                    <LabelList dataKey="Входящие" position="top" />
                </Bar>
                <Bar dataKey="Пропущенные" fill="#FF0000" >
                    <LabelList dataKey="Пропущенные" position="top"></LabelList>
                </Bar>
            </BarChart>
        );
    }
}

export default class extends Component {
    state = { periodMode: 'interval', interval: getCurrentWeek() }

    componentDidMount() {
        this.fetch()
    }

    onPeriodChange({ mode }) {
        this.setState({ periodMode: mode, interval: false })
    }

    onInterval({ start, end, intervalType }) {
        console.log('onInterval', { start, end, intervalType })
        this.setState(
            { interval: { start, end, intervalType } },
            () => this.fetch()
        )
    }

    fetch() {
        this.setState({ stats: false }, () => {
            let url = `http://papi.mindsales-crm.com/stats/calls?token=${Cookies.get('msid')}`

            const { start, end, intervalType } = this.state.interval
            if (start) url = `${url}&start=${start}`
            if (end) url = `${url}&end=${end}`
            if (intervalType) url = `${url}&type=${intervalType}`

            axios.get(url)
                .then(({ data: { items } }) => {
                    this.setState({ stats: items })
                })
                .catch(console.log);
        })
    }


    render() {
        window.scrollTo(0, 0)

        const { periodMode, interval, stats } = this.state
        const graph = stats ? cleanData(stats) : false

        return (
            <section className="bContent b">
                <header className="bContent__header">Входящие и пропущенные звонки</header>
                <PeriodSelector mode={periodMode}
                    onChange={this.onPeriodChange.bind(this)} />

                {periodMode === 'interval' && <PeriodCalendar interval={interval} onInterval={this.onInterval.bind(this)} />}

                {periodMode === 'day' && <Calendar />}

                {graph.length > 0 && <SimpleBarChart data={graph} />}
                {graph.length === 0 && <p style={{ padding: '2rem 0', textAlign: 'center' }}>Звонков не было 🙈</p>}
                {graph === false && <p style={{ padding: '2rem 0', textAlign: 'center' }}>Загружаем статистику... 👀</p>}
            </section>
        );
    }
}


